/* loginpage start from here  */
input::placeholder {
  padding-left: 0.3rem;
}

.bg-auth {
  background-color: #55bf79;
  width: 100%;
  min-height: 100vh;
  color: #808c91;
}

.main-div {
  background-color: rgba(249, 250, 250, 0.8);
  padding: 2rem;
  border-radius: 1rem;
  width:40%;
  margin:0 auto;
}
.main-div h4 {
  font-family: "Poppins", sans-serif;
  font-weight: bolder;
  font-size: 2rem;
  text-align: center;
  padding-top: 1.3rem;
  color: #1b2c2e;
}
.auth-logo{
  position: relative;
}
.auth-logo img {
  width: 6rem;
  object-fit: cover;
  display: block;
  margin: 0 auto;
}

.form-control {
  font-family: "Poppins", sans-serif !important;
  display: block !important;
  width: 100% !important;
  padding: 1rem 1rem !important ;
  font-size: 1arem !important;
  font-weight: 400 !important;
  line-height: 1 !important;
  color: #212529 !important;
  background-color: #fff !important;
  margin: 1.2rem 0rem !important;
  transition: none !important;
  box-shadow: 1.5px 3px 6.75px 1.5px rgba(0, 0, 0, 0.03) !important;
  height: 40px;
  border-radius: 10px;
  border:none;
  outline: none;
}

.form-control:focus {
  color: #212529 !important;
  background-color: #fff !important;
  border-color: white !important;
  outline: 0 !important;
  box-shadow: 1.5px 3px 6.75px 1.5px rgba(0, 0, 0, 0.03) !important;
}
.login-btn Button {
  margin-top: 1rem;
  width: 100%;
  height: 40px;
  background-color: #158b3f;
  color: #ffffff;
  border-radius: 0.8rem;
  border: 0.1rem solid #158b3f;
  font-family: "Poppins", sans-serif;
  font-size: 1.2rem;
  font-weight: 400;
}
.auth-footer {
  text-align: center;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  margin-top: 9rem;
}
.auth-footer span {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  color: #808c91;
}
.auth-footer a {
  font-family: "Poppins", sans-serif;
  font-size: 1.7rem;
  font-weight: 500;
  text-decoration: none;
  color: #158b3f;
}
.auth-footer a:hover {
  color: #158b3f;
}

.forCheckbox label {
  display: block;
  padding-left: 15px;
  text-indent: -15px;
  font-family: "Poppin", sans-serif;
  font-size: 0.9rem;
  font-weight: 400;
  letter-spacing: 0.5px;
}
.forCheckbox input {
  width: 15px;
  height: 15px;
  padding: 0;
  margin: 0;
  vertical-align: middle;
}
.forForget a {
  font-family: "Poppins", sans-serif;
  color: #158b3f;
  text-decoration: none;
  font-size: 1rem;
}
.forForget a:hover {
  color: #158b3f;
}
.register{
  display: flex;
  justify-content: center;
  margin-top: 20px;
  text-decoration: none;
  color: #158b3f;
}
@media screen and (max-width:768px) {
  .main-div{
    width:100%;
    margin:0;
  }
}
/* loginpage end  */

/* signup page start from here  */

.main-div-signup {
  background-color: rgba(249, 250, 250, 0.8);
  padding: 2rem;
  border-radius: 1rem;
  height: 55rem;
  margin-top: 6rem;
}
.main-div-signup h4 {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-size: 2.3rem;
  text-align: center;
  padding-top: 1.3rem;
  color: #1b2c2e;
}
.auth-footer-signup {
  text-align: center;
  margin-top: 3rem;
}

.auth-footer-signup span {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  color: #808c91;
}
.auth-footer-signup a {
  font-family: "Poppins", sans-serif;
  font-size: 1.7rem;
  font-weight: 500;
  text-decoration: none;
  color: #158b3f;
}
.auth-footer-signup a:hover {
  color: #158b3f;
}
.adjust{
  display: flex;
  justify-content: center;
  align-items: center;
}
.adjust h4 {
  font-size: 0.9rem;
  color:#808c91;
  font-weight: 500;
  margin-right:10px;
}
.form-grid{
  display: flex;
  gap:45px;
}

/* signup page end here  */

/* forgetpassword page start fromm here  */

/* forget password page title  */

.forgettitle h5 {
  font-family: "Poppins", sans-serif;
  font-size: 2.3rem;
  font-weight: 500;
  text-align: center;
  padding-top: 2.3rem;
  color: #1b2c2e;
}
.forgettitle p {
  font-family: "Poppins", sans-serif;
  text-align: center;
  padding: 0.5rem;
  font-size: 1.5rem;
  color: #828282;
}
.reset-btn button {
  margin-top: 1rem;
  width: 100%;
  height: 4rem;
  background-color: #158b3f;
  color: #ffffff;
  border-radius: 0.8rem;
  border: 0.1rem solid #158b3f;
  font-family: "Poppins", sans-serif;
  font-size: 2rem;
  font-weight: 400;
}

.reset-footer {
  text-align: center;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  margin-top: 12rem;
}
.reset-footer span {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  color: #808c91;
}
.reset-footer a {
  font-family: "Poppins", sans-serif;
  font-size: 1.7rem;
  font-weight: 500;
  text-decoration: none;
  color: #158b3f;
}
.reset-footer a:hover {
  color: #158b3f;
}
.info{
  text-align: center;
  max-width:60%;
  display: block;
  margin:0 auto;
  font-size: 14px;
}

/* forgetpassword page end  */

/* edit profile page start from here  */
.forbackground {
  width: 140px;
  height: 140px;
  border-radius: 100%;
  margin: 0 auto;
  border: 2px solid #ffffff;
  position: relative;
}
.forbackground img {
  height: 100%;
  width: 100%;
  border-radius: 50%;
}
.icon svg {
  position: absolute;
  top: 110px;
  right: 10px;
  background: #e2e2e2;
  border-radius: 100%;
  width: 2.3rem;
  height: 2.3rem;
  object-fit: cover;
  line-height: 30px;
  vertical-align: middle;
  text-align: center;
  color: #338b40;
  cursor: pointer;
}
.profile-icon{
  width:30px;
  height: 30px;
  border-radius: 50%;
  background-color: #FFF;
  position: absolute;
  right:-260px;
  top:-40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  line-height: 0px;
}
.upload-btn-wrapper {
  position: relative;
}

.upload-btn-wrapper input[type=file] {
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
}
